<template>
  <v-container>
    <h1 class="subheading grey--text">All Orders</h1>
    <v-container fluid>
      <v-card flat>
        <v-data-table
          :headers="headers"
          :items="customers"
          hide-actions
          flat
          dark
        >
          <template v-slot:items="props" class="ma-5">
            <td class="text-xs-center">{{ props.item.id }}</td>
            <td class="text-xs-center">{{ props.item.name }}</td>
            <td class="text-xs-center"><v-chip :color="props.item.color" text-color="white">{{ props.item.orderStatus }}</v-chip></td>
            <td class="text-xs-center">
                <v-btn color="info" flat>
                    <v-icon>edit</v-icon>
                </v-btn>
            </td>
          </template>
        </v-data-table>
      </v-card>
    </v-container>
  </v-container>
</template>
<script>
/* eslint-disable */
export default {
  name: 'MarketingDashboard',
  data: () => {
    return {
      selectedType: 'all',
      statuscolor: 'red',
      statusType: [ 'all', 'compeleted', 'uncompeleted' ],
      headers: [
        {
          text: 'ID',
          align: 'center',
          sortable: false,
          value: 'id'
        },
        {
          text: 'Name',
          align: 'center',
          value: 'name',
          sortable: false
        },
        { text: 'orderStatus', align: 'center', value: 'orderStatus', sortable: false },
        { text: 'Setting', align: 'center', sortable: false }
      ],
      customers: [
        { 
          id: '1',
          name: 'Salman',
          orderStatus: 'compeleted',
          color: 'success',
        },
        { 
          id: '2',
          name: 'Ateeq',
          orderStatus: 'canceled',
          color: 'red',
        },
        { 
          id: '3',
          name: 'Ahmed',
          orderStatus: 'register',
          color: 'yellow',
        },
      ]
    }
  },
  computed: {
    ordorColor(){
      if(orderStatus == 'compeleted'){
        return 'green'
      }else if (orderStatus == 'register') {
        return 'yellow'
      } else {
        return 'red'
      }
    }
  }
}
</script>
<style scoped>
  .compeleted{
    border-center: 4px solid rgb(5, 252, 5);
  }
  .register{
    border-center: 4px solid yellow;
  }
  .canceled{
    border-center: 4px solid red;
  }
</style>

